/* General app container */
.App {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4; /* light gray background */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header styling */
.App-header {
  background-color: #282c34; /* dark header bar */
  padding: 20px;
  color: white;
  text-align: center;
}

.App-header h1 {
  margin: 0;
  font-size: 2rem;
}

/* Navigation bar */
nav {
  margin-top: 15px;
}

nav button {
  margin: 0 10px;
  padding: 8px 16px;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  background-color: #61dafb;
  color: #000;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

nav button:hover {
  background-color: #21a1f1;
}

/* Main content area */
main {
  flex: 1;
  padding: 40px;
  text-align: center;
  font-size: 1.4rem;
  color: #333;
}